
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 1), (min-device-pixel-ratio: 1) {
.border-1px:after {
    -webkit-transform: scaleY(1);
    transform: scaleY(1);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: 'block';
  content: '';
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
#app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
#app .tab:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,21,0.1);
  content: ' ';
}
#app .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#app .tab .tab-item > a {
  display: block;
  text-decoration: none;
  font-size: 14px;
  color: #4d555d;
}
#app .tab .tab-item > a.router-link-active {
  color: #f00;
}


