
.seller {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .overview {
  position: relative;
  padding: 18px;
}
.seller .overview .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .overview .desc {
  padding-bottom: 18px;
  line-height: 18px;
  position: relative;
  font-size: 0;
}
.seller .overview .desc:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .overview .desc .star {
  display: inline-block;
  margin-top: 2px;
  margin-right: 10px;
  vertical-align: top;
}
.seller .overview .desc .text {
  display: inline-block;
  vertical-align: top;
  line-height: 18px;
  font-size: 10px;
  color: #4d555d;
}
.seller .overview .remark {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
}
.seller .overview .remark .block {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .overview .remark .block:last-child {
  border: none;
}
.seller .overview .remark .block h2 {
  margin-bottom: 4px;
  line-height: 10px;
  font-size: 10px;
  color: #93999f;
}
.seller .overview .remark .block .content {
  line-height: 24px;
  font-size: 10px;
  color: #07111b;
}
.seller .overview .remark .block .content .stress {
  font-size: 24px;
}
.seller .overview .favorite {
  position: absolute;
  width: 50px;
  right: 11px;
  top: 18px;
  text-align: center;
}
.seller .overview .favorite .icon-favorite {
  display: block;
  margin-bottom: 4px;
  line-height: 24px;
  font-size: 24px;
  color: #d4d6d9;
}
.seller .overview .favorite .icon-favorite.active {
  color: #f01414;
}
.seller .overview .favorite .icon-favorite .text {
  line-height: 10px;
  font-size: 14px;
  color: #4d195d;
}
.seller .bulletin {
  padding: 18px;
}
.seller .bulletin .title {
  margin-bottom: 8px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .bulletin .content-wrapper {
  padding: 0 12px 16px 12px;
  position: relative;
}
.seller .bulletin .content-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .bulletin .content-wrapper .content {
  line-height: 24px;
  font-size: 12px;
  color: #f01414;
}
.seller .bulletin .supports {
  margin: 24px auto 0 auto;
  padding: 0 12px;
  width: 80%;
}
.seller .bulletin .supports .supports-item {
  padding-bottom: 12px;
}
.seller .bulletin .supports .supports-item:last-child {
  padding-bottom: 0;
}
.seller .bulletin .supports .supports-item .list-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  vertical-align: middle;
  margin-left: 6px;
}
.seller .bulletin .supports .supports-item.decrease .list-icon {
  background-image: url("decrease_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item.decrease .list-icon {
    background-image: url("decrease_4@3x.png");
}
}
.seller .bulletin .supports .supports-item.discount .list-icon {
  background-image: url("discount_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item.discount .list-icon {
    background-image: url("discount_4@3x.png");
}
}
.seller .bulletin .supports .supports-item.special .list-icon {
  background-image: url("special_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item.special .list-icon {
    background-image: url("special_4@3x.png");
}
}
.seller .bulletin .supports .supports-item.invoice .list-icon {
  background-image: url("invoice_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item.invoice .list-icon {
    background-image: url("invoice_4@3x.png");
}
}
.seller .bulletin .supports .supports-item.guarantee .list-icon {
  background-image: url("guarantee_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .bulletin .supports .supports-item.guarantee .list-icon {
    background-image: url("guarantee_4@3x.png");
}
}
.seller .bulletin .supports .supports-item .list-text {
  font-size: 12px;
  font-weight: 200;
  padding-left: 6px;
}
.seller .pics {
  padding: 18px;
}
.seller .pics .title {
  margin-bottom: 12px;
  line-height: 14px;
  color: #07111b;
  font-size: 14px;
}
.seller .pics .pic-wrapper {
  width: 100%;
  overflow: hidden;
}
.seller .pics .pic-wrapper .pic-list {
  white-space: nowrap;
  font-size: 0;
/* 宽度通过计算获得 */
  border: 1px solid #f00;
}
.seller .pics .pic-wrapper .pic-list .pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 120px;
  height: 90px;
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child {
  margin: 0;
}
.seller .info {
  padding: 18px 18px 0 18px;
  color: #07111b;
}
.seller .info .title {
  padding-bottom: 12px;
  line-height: 14px;
  position: relative;
  font-size: 14px;
}
.seller .info .title:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .info .info-item {
  padding: 16px 12px;
  line-height: 16px;
  position: relative;
}
.seller .info .info-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.seller .info .info-item:last-child {
  border: 0;
  border: none;
}


