
.shopcart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
}
.shopcart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
}
.shopcart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
/* overflow: hidden不可以加 一加就不显示了 */
}
.shopcart .content .content-left .logo-wrapper {
  position: relative;
  top: -10px;
  float: left;
  background: #141d27;
  width: 56px;
  height: 56px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 6px;
  margin: 0 12px;
  vertical-align: top;
  border-radius: 50%;
}
.shopcart .content .content-left .logo-wrapper .logo {
  text-align: center;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.4);
  border-radius: 50%;
}
.shopcart .content .content-left .logo-wrapper .logo.heighlight {
  background: #00a0dc;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart {
  color: #141d27;
  display: block;
  width: 100%;
  height: 100%;
  font-size: 24px;
  line-height: 44px;
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart.heighlight {
  color: #fff;
}
.shopcart .content .content-left .logo-wrapper .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  text-align: center;
  font-size: 9px;
  line-height: 16px;
  font-weight: 700;
  color: #fff;
  background: #f01414;
  border-radius: 16px;
}
.shopcart .content .content-left .price {
  float: left;
  color: #fff;
  line-height: 24px;
  font-size: 16px;
  margin-top: 10px;
  padding-left: 2px;
  padding-right: 6px;
  border-right: 1px solid #fff;
  font-weight: 200;
  color: rgba(255,255,255,0.4);
}
.shopcart .content .content-left .price.heighlight {
  color: #fff;
}
.shopcart .content .content-left .desc {
  float: left;
  color: rgba(255,255,255,0.4);
  line-height: 24px;
  font-size: 14px;
  margin-top: 10px;
  padding-left: 4px;
}
.shopcart .content .content-left .desc.enough {
  color: #fff;
}
.shopcart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 105px;
          flex: 0 0 105px;
  background: rgba(0,0,0,0.2);
  width: 105px;
  font-size: 12px;
  font-weight: 700;
  line-height: 46px;
  color: rgba(255,255,255,0.4);
  text-align: center;
}
.shopcart .content .content-right.enough {
  background: #00b43c;
  color: #fff;
  font-weight: 700;
}
.shopcart .ball-container .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  -webkit-transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
/* 小球的落点位置 */
}
.shopcart .ball-container .ball .inner {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.shopcart .shopcart-list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  -webkit-transform: translate3d(0, -100%, 0) /* 没有动画的自然状态 在最顶端 */;
          transform: translate3d(0, -100%, 0) /* 没有动画的自然状态 在最顶端 */;
}
.shopcart .shopcart-list.fold-enter-active,
.shopcart .shopcart-list.fold-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.shopcart .shopcart-list.fold-enter,
.shopcart .shopcart-list.fold-leave-to {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopcart .shopcart-list .list-header {
  height: 40px;
  line-height: 40px;
  background: #f3f5f7;
  padding: 0 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  overflow: hidden;
}
.shopcart .shopcart-list .list-header .title {
  float: left;
  font-size: 14px;
  font-weight: 200;
  color: #07111b;
  padding-left: 18px;
}
.shopcart .shopcart-list .list-header .empty {
  float: right;
  right: 8px;
  font-size: 12px;
  color: #00a0dc;
  padding: 0 10px;
}
.shopcart .shopcart-list .list-content {
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
  background: #fff;
}
.shopcart .shopcart-list .list-content .food {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
  padding: 12px 0;
  -background: #f00;
}
.shopcart .shopcart-list .list-content .food:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.shopcart .shopcart-list .list-content .food .name {
  font-size: 14px;
  color: #07111b;
  line-height: 24px;
}
.shopcart .shopcart-list .list-content .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700;
  color: #f01414;
}
.shopcart .shopcart-list .list-content .food .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 2px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  background: rgba(7,17,27,0.6);
  opacity: 1;
}
.list-mask.fadebac-enter-active,
.list-mask.fadebac-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.list-mask.fadebac-enter,
.list-mask.fadebac-leave-to {
  opacity: 0;
  background: rgba(7,17,27,0);
}


