
.goods {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 177px;
  bottom: 46px;
  width: 100%;
  overflow: hidden;
}
.goods .menu-wrapper {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 80px;
          flex: 0 1 80px;
  width: 80px /* 兼容安卓浏览器 */;
  background: #f3f5f7;
}
.goods .menu-wrapper .menu-item {
  display: table;
  height: 54px;
  width: 56px;
  line-height: 14px;
  font-size: 12px;
  padding: 0 12px;
}
.goods .menu-wrapper .menu-item .menu-text {
  display: table-cell;
  position: relative;
  width: 56px;
  vertical-align: middle;
  font-size: 12px;
  font-weight: 200;
  line-height: 14px;
  margin-left: 4px;
  text-align: center;
}
.goods .menu-wrapper .menu-item .menu-text:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.goods .menu-wrapper .menu-item .menu-text .menu-icon {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
  background-repeat: no-repeat;
  background-size: 12px 12px;
  margin-left: 2px;
}
.goods .menu-wrapper .menu-item .menu-text .menu-icon.decrease {
  background-image: url("decrease_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .menu-text .menu-icon.decrease {
    background-image: url("decrease_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .menu-text .menu-icon.discount {
  background-image: url("discount_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .menu-text .menu-icon.discount {
    background-image: url("discount_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .menu-text .menu-icon.special {
  background-image: url("special_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .menu-text .menu-icon.special {
    background-image: url("special_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .menu-text .menu-icon.invoice {
  background-image: url("invoice_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .menu-text .menu-icon.invoice {
    background-image: url("invoice_3@3x.png");
}
}
.goods .menu-wrapper .menu-item .menu-text .menu-icon.guarantee {
  background-image: url("guarantee_3@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.goods .menu-wrapper .menu-item .menu-text .menu-icon.guarantee {
    background-image: url("guarantee_3@3x.png");
}
}
.goods .menu-wrapper .current {
  background: #fff;
  color: #07111b;
  line-height: 14px;
  font-weight: 700;
  position: relative;
  margin-top: -1px;
  z-index: 10;
}
.goods .menu-wrapper .current>.menu-text:after {
  display: none;
}
.goods .foods-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods .foods-wrapper .food-list .title {
  color: #93999f;
  background-color: #f3f5f7;
  padding-left: 14px;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  line-height: 26px;
}
.goods .foods-wrapper .food-list .food-main li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  position: relative;
  padding: 18px;
}
.goods .foods-wrapper .food-list .food-main li:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.goods .foods-wrapper .food-list .food-main li .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 57px;
          flex: 0 1 57px;
}
.goods .foods-wrapper .food-list .food-main li .icon img {
  width: 57px;
  height: 57px;
  border-radius: 2px;
}
.goods .foods-wrapper .food-list .food-main li .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 10px;
}
.goods .foods-wrapper .food-list .food-main li .content .name {
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
  padding-top: 2px;
  padding-bottom: 8px;
}
.goods .foods-wrapper .food-list .food-main li .content .description {
  font-size: 10px;
  color: #93999f;
  line-height: 18px;
  padding-bottom: 8px;
}
.goods .foods-wrapper .food-list .food-main li .content .extra .sellCount,
.goods .foods-wrapper .food-list .food-main li .content .extra .rating {
  font-size: 10px;
  color: #93999f;
  font-weight: normal;
  line-height: 24px;
}
.goods .foods-wrapper .food-list .food-main li .content .extra 。rating {
  padding-left: 12px;
}
.goods .foods-wrapper .food-list .food-main li .content .price {
  padding-bottom: 18p;
}
.goods .foods-wrapper .food-list .food-main li .content .price .sale {
  font-size: 10px;
  color: #f01414;
  font-weight: normal;
  line-height: 24px;
}
.goods .foods-wrapper .food-list .food-main li .content .price .sale .sale-price {
  font-weight: 700;
  font-size: 14px;
}
.goods .foods-wrapper .food-list .food-main li .content .price .oldprice {
  font-size: 0;
  color: #93999f;
  line-height: 24px;
  text-decoration: line-through;
}
.goods .foods-wrapper .food-list .food-main li .content .cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 12px;
}


