
.cartcontrol {
  overflow: hidden;
}
.cartcontrol .cart-decrease {
  float: left;
  font-size: 24px;
  line-height: 24px;
  padding: 6px;
  color: #00a0dc;
}
.cartcontrol .cart-decrease.move-transition-enter-active,
.cartcontrol .cart-decrease.move-transition-leave-active {
  opacity: 1;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-transform: translate3D(0, 0, 0) rotate(0) /* 开启硬件加速 让动画变得更流畅 */;
          transform: translate3D(0, 0, 0) rotate(0) /* 开启硬件加速 让动画变得更流畅 */;
}
.cartcontrol .cart-decrease.move-transition-enter-active .inner,
.cartcontrol .cart-decrease.move-transition-leave-active .inner {
  opacity: 1;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
  -webkit-transform: translate3D(0, 0, 0) rotate(0);
          transform: translate3D(0, 0, 0) rotate(0);
}
.cartcontrol .cart-decrease.move-transition-enter,
.cartcontrol .cart-decrease.move-transition-leave-to {
  -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
          transform: translate3D(24px, 0, 0) rotate(180deg);
  opacity: 0;
}
.cartcontrol .cart-decrease.move-transition-enter .inner,
.cartcontrol .cart-decrease.move-transition-leave-to .inner {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0) rotate(180deg);
          transform: translate3D(24px, 0, 0) rotate(180deg);
}
.cartcontrol .cart-count {
  float: left;
  font-size: 10px;
  text-align: center;
  vertical-align: top;
  line-height: 24px;
  width: 12px;
  height: 24px;
  padding-top: 6px;
  color: #93999f;
}
.cartcontrol .cart-add {
  float: left;
  font-size: 24px;
  line-height: 24px;
  padding: 6px;
  color: #00a0dc;
}


